"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Bell, Menu, Plus, Search } from "lucide-react"
import { useAuthStore } from "@/lib/auth-store"
import { ManagerSidebar } from "@/components/manager/manager-sidebar"
import { DashboardStats } from "@/components/manager/dashboard-stats"
import { AppointmentsList } from "@/components/manager/appointments-list"
import { PopularServices } from "@/components/manager/popular-services"
import { StaffPlanning } from "@/components/manager/staff-planning"
import { AnalyticsTab } from "@/components/manager/analytics-tab"
import { ClientsTab } from "@/components/manager/clients-tab"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { EmployeePerformance } from "@/components/manager/employee-perfomance"
import { ServicesTab } from "@/components/manager/service-tab"
import { StaffTab } from "@/components/manager/staff-tab"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { useToast } from "@/components/ui/use-toast"

// Types
interface Client {
  id: string
  firstName: string
  lastName: string
  phone: string
  email?: string
  notes?: string
}

interface Appointment {
  id: string
  time: string
  date: string // "2025-04-05"
  client: string
  clientPhone: string
  service: string
  stylist: string
  status: "confirmed" | "arrived" | "inprogress" | "completed"
  origin: "online" | "instore"
}

export default function ManagerDashboard() {
  const router = useRouter()
  const { user, isAuthenticated } = useAuthStore()
  const [sidebarOpen, setSidebarOpen] = useState(false)
  const [activeTab, setActiveTab] = useState("dashboard")
  const { toast } = useToast()

  // États partagés entre les formulaires et la liste
  const [clients, setClients] = useState<Client[]>([])
  const [appointments, setAppointments] = useState<Appointment[]>([])

  // Modals
  const [openClientModal, setOpenClientModal] = useState(false)
  const [openAppointmentModal, setOpenAppointmentModal] = useState(false)

  useEffect(() => {
    if (!isAuthenticated || (user?.role !== "gerant" && user?.role !== "admin")) {
      router.push("/auth/login")
    }
  }, [isAuthenticated, user, router])

  if (!isAuthenticated || (user?.role !== "gerant" && user?.role !== "admin")) {
    return null
  }

  // === FORMULAIRE CLIENT ===
  const ClientForm = () => {
    const [firstName, setFirstName] = useState("")
    const [lastName, setLastName] = useState("")
    const [phone, setPhone] = useState("")
    const [email, setEmail] = useState("")
    const [notes, setNotes] = useState("")

    const handleSubmit = (e: React.FormEvent) => {
      e.preventDefault()
      const newClient: Client = {
        id: Date.now().toString(),
        firstName,
        lastName,
        phone,
        email: email || undefined,
        notes: notes || undefined,
      }
      setClients(prev => [...prev, newClient])
      toast({
        title: "Client ajouté !",
        description: `${firstName} ${lastName} a été enregistré.`,
      })
      setOpenClientModal(false)
    }

    return (
      <form onSubmit={handleSubmit} className="space-y-4">
        <div className="grid grid-cols-2 gap-4">
          <div>
            <Label>Prénom</Label>
            <Input required value={firstName} onChange={e => setFirstName(e.target.value)} placeholder="Marie" />
          </div>
          <div>
            <Label>Nom</Label>
            <Input required value={lastName} onChange={e => setLastName(e.target.value)} placeholder="Dubois" />
          </div>
        </div>
        <div>
          <Label>Téléphone *</Label>
          <Input required value={phone} onChange={e => setPhone(e.target.value)} placeholder="06 12 34 56 78" />
        </div>
        <div>
          <Label>Email (optionnel)</Label>
          <Input type="email" value={email} onChange={e => setEmail(e.target.value)} placeholder="marie.dubois@gmail.com" />
        </div>
        <div>
          <Label>Notes</Label>
          <textarea
            className="w-full min-h-[80px] rounded-md border border-input bg-background px-3 py-2 text-sm"
            value={notes}
            onChange={e => setNotes(e.target.value)}
            placeholder="Allergies, préférences..."
          />
        </div>
        <div className="flex justify-end gap-3 pt-4">
          <Button type="button" variant="outline" onClick={() => setOpenClientModal(false)}>
            Annuler
          </Button>
          <Button type="submit">Enregistrer le client</Button>
        </div>
      </form>
    )
  }

  // === FORMULAIRE RENDEZ-VOUS ===
  const AppointmentForm = () => {
    const [searchClient, setSearchClient] = useState("")
    const [selectedClient, setSelectedClient] = useState<Client | null>(null)
    const [service, setService] = useState("")
    const [stylist, setStylist] = useState("")
    const [date, setDate] = useState("")
    const [time, setTime] = useState("")
    const [notes, setNotes] = useState("")

    const services = ["Tresses Africaines", "Coupe Homme", "Coupe Femme", "Soin Profond", "Défrisage", "Coloration", "Brushing", "Barbe"]
    const stylists = ["Amina", "Fatou", "Yasmine", "Sophie", "Lucas"]

    const filteredClients = clients.filter(c =>
      `${c.firstName} ${c.lastName}`.toLowerCase().includes(searchClient.toLowerCase()) ||
      c.phone.includes(searchClient)
    )

    const handleSubmit = (e: React.FormEvent) => {
      e.preventDefault()
      if (!selectedClient) return

      const newApt: Appointment = {
        id: Date.now().toString(),
        time,
        date,
        client: `${selectedClient.firstName} ${selectedClient.lastName}`,
        clientPhone: selectedClient.phone,
        service,
        stylist,
        status: "confirmed",
        origin: Math.random() > 0.5 ? "online" : "instore",
      }

      setAppointments(prev => [...prev, newApt])
      toast({
        title: "Rendez-vous créé !",
        description: `${service} avec ${stylist} le ${new Date(date).toLocaleDateString("fr-FR")} à ${time}`,
      })
      setOpenAppointmentModal(false)
    }

    return (
      <form onSubmit={handleSubmit} className="space-y-5">
        {/* Recherche client */}
        <div>
          <Label>Client</Label>
          <div className="relative">
            <Search className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
            <Input
              className="pl-10"
              placeholder="Rechercher un client par nom ou téléphone..."
              value={searchClient}
              onChange={e => setSearchClient(e.target.value)}
            />
          </div>
          {searchClient && filteredClients.length > 0 && (
            <div className="mt-2 border rounded-md max-h-48 overflow-y-auto">
              {filteredClients.map(client => (
                <button
                  key={client.id}
                  type="button"
                  className={`w-full text-left px-4 py-2 hover:bg-accent ${selectedClient?.id === client.id ? "bg-accent" : ""}`}
                  onClick={() => {
                    setSelectedClient(client)
                    setSearchClient(`${client.firstName} ${client.lastName}`)
                  }}
                >
                  <div className="font-medium">{client.firstName} {client.lastName}</div>
                  <div className="text-sm text-muted-foreground">{client.phone}</div>
                </button>
              ))}
            </div>
          )}
          {selectedClient && (
            <div className="mt-2 p-3 bg-muted rounded-md">
              Sélectionné : <strong>{selectedClient.firstName} {selectedClient.lastName}</strong> ({selectedClient.phone})
            </div>
          )}
        </div>

        <div className="grid grid-cols-2 gap-4">
          <div>
            <Label>Service</Label>
            <select className="w-full rounded-md border border-input bg-background px-3 py-2" required value={service} onChange={e => setService(e.target.value)}>
              <option value="">Choisir...</option>
              {services.map(s => <option key={s} value={s}>{s}</option>)}
            </select>
          </div>
          <div>
            <Label>Coiffeur/se</Label>
            <select className="w-full rounded-md border border-input bg-background px-3 py-2" required value={stylist} onChange={e => setStylist(e.target.value)}>
              <option value="">Choisir...</option>
              {stylists.map(s => <option key={s} value={s}>{s}</option>)}
            </select>
          </div>
        </div>

        <div className="grid grid-cols-2 gap-4">
          <div>
            <Label>Date</Label>
            <Input type="date" required value={date} onChange={e => setDate(e.target.value)} min={new Date().toISOString().split("T")[0]} />
          </div>
          <div>
            <Label>Heure</Label>
            <Input type="time" required value={time} onChange={e => setTime(e.target.value)} />
          </div>
        </div>

        <div>
          <Label>Notes (optionnel)</Label>
          <textarea
            className="w-full min-h-[80px] rounded-md border border-input bg-background px-3 py-2 text-sm"
            value={notes}
            onChange={e => setNotes(e.target.value)}
          />
        </div>

        <div className="flex justify-end gap-3 pt-4">
          <Button type="button" variant="outline" onClick={() => setOpenAppointmentModal(false)}>
            Annuler
          </Button>
          <Button type="submit" disabled={!selectedClient || !service || !stylist || !date || !time}>
            Créer le rendez-vous
          </Button>
        </div>
      </form>
    )
  }

  return (
    <div className="flex h-screen bg-gray-50 dark:bg-gray-950">
      <ManagerSidebar isOpen={sidebarOpen} onClose={() => setSidebarOpen(false)} activeTab={activeTab} onTabChange={setActiveTab} />

      <div className="flex-1 flex flex-col overflow-hidden">
        {/* Header */}
        <header className="bg-white dark:bg-gray-900 border-b min-h-16 md:h-20 flex items-center justify-between px-4 md:px-6 gap-2">
          <div className="flex items-center gap-2 md:gap-4 min-w-0">
            <Button variant="ghost" size="icon" className="lg:hidden shrink-0" onClick={() => setSidebarOpen(true)}>
              <Menu className="w-5 h-5" />
            </Button>
            <h1 className="text-lg md:text-2xl font-bold truncate">Tableau de bord</h1>
          </div>
          <div className="flex items-center gap-2 md:gap-3 shrink-0">
            <Button
              variant="outline"
              className="hidden sm:flex bg-cyan-500 hover:bg-cyan-600 text-white border-cyan-500"
              onClick={() => setOpenClientModal(true)}
            >
              <Plus className="w-4 h-4 mr-2" />
              <span className="hidden md:inline">Enregistrer un client</span>
              <span className="md:hidden">Client</span>
            </Button>

            <Button className="bg-cyan-500 hover:bg-cyan-600" onClick={() => setOpenAppointmentModal(true)}>
              <Plus className="w-4 h-4 mr-2" />
              <span className="hidden md:inline">Ajouter un rendez-vous</span>
              <span className="md:hidden">RDV</span>
            </Button>

            <Button variant="ghost" size="icon" className="shrink-0">
              <Bell className="w-5 h-5" />
            </Button>
          </div>
        </header>

        {/* Modals */}
        <Dialog open={openClientModal} onOpenChange={setOpenClientModal}>
          <DialogContent className="max-w-2xl">
            <DialogHeader>
              <DialogTitle>Nouveau client</DialogTitle>
            </DialogHeader>
            <ClientForm />
          </DialogContent>
        </Dialog>

        <Dialog open={openAppointmentModal} onOpenChange={setOpenAppointmentModal}>
          <DialogContent className="max-w-3xl max-h-[90vh] overflow-y-auto">
            <DialogHeader>
              <DialogTitle>Nouveau rendez-vous</DialogTitle>
            </DialogHeader>
            <AppointmentForm />
          </DialogContent>
        </Dialog>

        {/* Main Content */}
        <main className="flex-1 overflow-y-auto p-4 md:p-6">
          <div className="max-w-7xl mx-auto space-y-4 md:space-y-6">
            {/* Passe les RDV au composant */}
            <AppointmentsList appointments={appointments} />

            {/* Le reste de ton dashboard */}
            {activeTab === "dashboard" && (
              <>
                <DashboardStats />
                <Tabs defaultValue="overview" className="w-full">
                  {/* ... ton code existant ... */}
                </Tabs>
              </>
            )}
            {/* ... autres tabs ... */}
          </div>
        </main>
      </div>
    </div>
  )
}